



































































import Vue from 'vue';
import { Skill } from 'ironquest';
import { capitalize, map } from 'lodash';

const SKILLS = [
  Skill.AGILITY,
  Skill.ATTACK,
  Skill.CONSTITUTION,
  Skill.CONSTRUCTION,
  Skill.COOKING,
  Skill.CRAFTING,
  Skill.DEFENCE,
  Skill.DIVINATION,
  Skill.DUNGEONEERING,
  Skill.FARMING,
  Skill.FIREMAKING,
  Skill.FISHING,
  Skill.FLETCHING,
  Skill.HERBLORE,
  Skill.HUNTER,
  Skill.INVENTION,
  Skill.MAGIC,
  Skill.MINING,
  Skill.PRAYER,
  Skill.RANGED,
  Skill.RUNECRAFTING,
  Skill.SLAYER,
  Skill.SMITHING,
  Skill.STRENGTH,
  Skill.SUMMONING,
  Skill.THIEVING,
  Skill.WOODCUTTING,
] as Skill[];

export default Vue.extend({
  name: 'settings',
  props: {
    value: {
      type: Object,
      required: true,
    },
  },
  computed: {
    skillOptions() {
      return map(SKILLS, skill => {
        return {
          value: skill,
          text: capitalize(skill),
        };
      });
    },
  },
  watch: {
    value() {
      this.$emit('input', this.value);
    },
  },
});
